import React from "react";
import './IDBIE1.css';
import Dexie from 'dexie'
import { importDB } from 'dexie-export-import';
import FileChooseLabelActFuncState from '../../FileChouseLabelAct/FuncState/FileChooseLabelActFuncState'


export default function ImportFile({ updateReportAsync, progressCallback }) {

    const importFile = async file => {
        try {
            await Dexie.delete('exportSample');
            console.log("Importing " + file.name);
            await importDB(file, { progressCallback });
            console.log("Import complete");
            await updateReportAsync();
        } catch (error) {
            console.error('' + error);
        }
    }
    return < >
        <FileChooseLabelActFuncState onFileUpload={importFile} />
    </>;
}