import React, { useState, useEffect } from "react";
import './IDBIE1.css';
import db from './db.js';
import ImportFile from './ImportFile';
import ExportFile from './ExportFile';



export default function IDBIE1() {
    const [tables, setTables] = useState([]);

    const getTablesAsync = async () => {
        if (db.hasBeenClosed())
            db.open();
        return await Promise.all(db.tables.map(async table => (
            <tr key={table.name}>
                <th>Table: {table.name}</th>
                <td>Primary Key:{table.schema.primKey.src}</td>
                <td>Row count: {await table.count()}</td>
            </tr>
        )))
    };

    const updateReportAsync = async () => setTables(await getTablesAsync());

    useEffect(() => {
        // if (tables.length) return;
        getTablesAsync()
            .then(tables => setTables(tables))
        // return ()=>db.close();
    })

    return < >

        <div className="column">
            <h3>Export</h3>
            <ExportFile />
        </div>

        <div className="column">
            <h3>Import</h3>
            <ImportFile updateReportAsync={updateReportAsync} progressCallback={progressCallback} />
        </div>
        <div className="column">
            <h3>Database Info</h3>
            <button onClick={updateReportAsync}>Update report</button>
            <table border="1">
                <thead></thead>
                <tbody>
                    <tr>
                        <th>Database Name</th>
                        <td colSpan="2">${db.name}</td>
                    </tr>
                    {tables}
                </tbody>
            </table>
        </div>
    </>;
}

function progressCallback({ totalRows, completedRows }) {
    console.log(`Progress: ${completedRows} of ${totalRows} rows completed`);
}