import React from "react";
import download from 'downloadjs';
import { exportDB } from 'dexie-export-import';
import db from './db.js';

export default function ExportFile({ progressCallback }) {
    const exportDatabase = async () => {
        try {
            const blob = await exportDB(db, { prettyJson: true, progressCallback });
            download(blob, "dexie-export.json", "application/json");
        } catch (error) {
            console.error('' + error);
        }
    };
    return <button onClick={exportDatabase}>
        Экспорт базы данных в json файл.
    </button>;
}