import Dexie from "dexie";
console.log("new Dexie by db.js")
const db = new Dexie('exportSample');
db.version(1).stores({
    foos: 'id',
    bars: 'ID1',
});

db.on('populate', () => {
    console.log("db.foos.bulkAdd")
    db.foos.bulkAdd([
        {
            id: 1,
            foo: 'foo1',
            date: new Date(),
        }, {
            id: 2,
            foo: 'foo2',
        }
    ]);
    db.bars.bulkAdd([
        {
            ID1: 1,
            bar: 'bar1',
            date: new Date(),
        }
    ])
});

export default db;


