import React, { useState } from "react";
import {
  BrowserRouter,
  Switch,
  Route,
  Link,
  NavLink
} from "react-router-dom";
import Home from './pages/Home';
import About from './pages/About';
import Help from './pages/Help';
import 'bootstrap/dist/css/bootstrap.css';
import './css/site.css'
import './css/hamburgers.css'
import './css/hamburger.css'
export default function App(props) {
  const [collapseNavMenu, setCollapseNavMenu] = useState(true);
  return <div className="app">
    <BrowserRouter basename={process.env.PUBLIC_URL + '/'}>
      <div className="sidebar">
        <div className="top-row pl-4 navbar navbar-dark" >

          <button
            aria-label="Меню"
            className={`navbar-toggler hamburger--collapse  ${collapseNavMenu ? "" : "is-active"} `}
            onClick={() => setCollapseNavMenu(!collapseNavMenu)}>
            <span className="hamburger-box">
              <span className="hamburger-inner" ></span>
            </span>
          </button>

          <Link to="/about" className="navbar-brand"> О нас</Link>
        </div>
        <nav className={collapseNavMenu ? "collapse" : null}>
          <ul className="nav flex-column">
            <li className="nav-item px-3">
              <NavLink to="/" className="nav-link" onClick={() => setCollapseNavMenu(true)} exact>
                <span className="oi oi-home" aria-hidden="true"></span>Домой
              </NavLink>
            </li>
            <li className="nav-item px-3" >
              <NavLink to="/about" className="nav-link" onClick={() => setCollapseNavMenu(true)}>
                <span className="oi oi-sun" aria-hidden="true"></span>О нас
              </NavLink>
            </li>
            <li className="nav-item px-3" >
              <NavLink to="/help" className="nav-link" onClick={() => setCollapseNavMenu(true)}>
                <span className="oi oi-question-mark" aria-hidden="true"></span>Помощь
              </NavLink>
            </li>
          </ul>
        </nav>
      </div>
      <div className="main">
        <div className="top-row px-4">
          <a href="https://ru.reactjs.org/" target="_blank" className="ml-md-auto" rel="noopener noreferrer">About</a>
        </div>
        <div className="content px-4">
          <Switch >
            <Route path="/" exact >
              <Home />
            </Route>
            <Route path="/about">
              <About />
            </Route>
            <Route path="/help">
              <Help />
            </Route>
          </Switch>
        </div>
      </div>
    </BrowserRouter>
  </div>;
}
